
.stats-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-bottom: 20%;
}
.slide-enter {
  opacity: 0;
  transform: translateY(30px);
}
.slide-enter-active {
  transition: all .1s ease;
  opacity: 1;
}
.slide-leave {
  opacity: 1;
}
.slide-leave-active {
  transition: all .1s ease;
  opacity: 0;
  transform: translateY(-30px);
}



