
#word {
  font-size: 2.5em;
  margin-bottom: 0;
}
.word-container {
  border-bottom: 1px solid white;
  cursor: pointer;
}
.reveal-enter-active {
  opacity: 0;
  transition: opacity 40s;
}
.reveal-leave-active {
  opacity: .1;
  transition: opacity .2s;
}
.reveal-enter-to {
  opacity: 1;
}
.reveal-leave-to {
  opacity: 0;
}



